import React, { Component, Suspense } from 'react'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import './scss/style.scss'

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

// Containers
const DefaultLayout = React.lazy(() => import('./layout/DefaultLayout'))

// Pages
const Report = React.lazy(() => import('./views/fitur/report/Report'))
const Login = React.lazy(() => import('./views/pages/login/Login'))
const UpdateReport = React.lazy(() => import('./views/fitur/update-report/UpdateReport'))


class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <Suspense fallback={loading}>
          <Routes>
            <Route path="/reports" name="Report Page" element={<Report />} />
            <Route path="/login" name="Login Page" element={<Login />} />
            <Route path="update-report/:id" element={<UpdateReport/>}/>
            <Route path="*" name="Home" element={<DefaultLayout />} />
          </Routes>
        </Suspense>
      </BrowserRouter>
    )
  }
}

export default App
